/* Global Reset */
*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
}

body {
  font-family: var(--font-sans);
  background: var(--bg-primary);
  color: var(--text-primary);
  line-height: 1.6;
  min-height: 100vh;
  transition: background-color var(--transition-base), color var(--transition-base);
}

/* Theme Selector */
.theme-selector-container {
  position: fixed;
  top: var(--space-lg);
  right: var(--space-lg);
  z-index: 1000;
}

.theme-toggle {
  min-width: 160px;
  height: 50px;
  padding: 0 var(--space-lg);
  border-radius: var(--rounded-full);
  background: var(--glass-bg);
  backdrop-filter: blur(var(--glass-blur));
  border: 2px solid var(--glass-border);
  box-shadow: var(--shadow-md);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all var(--transition-base);
  color: var(--text-primary);
  font-weight: 600;
  font-size: 0.95rem;
}

.theme-toggle:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
  border-color: var(--deep-sea-teal);
}

.theme-menu {
  position: absolute;
  top: 60px;
  right: 0;
  min-width: 280px;
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border: 2px solid var(--glass-border);
  border-radius: var(--rounded-lg);
  box-shadow: var(--shadow-xl);
  padding: var(--space-sm);
  animation: slideDown 0.3s cubic-bezier(0.16, 1, 0.3, 1);
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.theme-option {
  width: 100%;
  padding: var(--space-md);
  background: transparent;
  border: 2px solid transparent;
  border-radius: var(--rounded);
  cursor: pointer;
  transition: all var(--transition-fast);
  text-align: left;
  margin-bottom: var(--space-xs);
  display: flex;
  flex-direction: column;
  gap: var(--space-xs);
}

.theme-option:hover {
  background: var(--bg-tertiary);
  border-color: var(--deep-sea-teal);
  transform: translateX(4px);
}

.theme-option.active {
  background: var(--bg-tertiary);
  border-color: var(--deep-sea-teal);
}

.theme-option-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.theme-option-label {
  font-weight: 600;
  color: var(--text-primary);
  font-size: 0.95rem;
}

.theme-checkmark {
  color: var(--deep-sea-teal);
  font-weight: 700;
  font-size: 1.1rem;
}

.theme-option-description {
  font-size: 0.85rem;
  color: var(--text-tertiary);
  line-height: 1.4;
}

/* Loading State */
.loading {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  font-size: 1.5rem;
  color: var(--text-secondary);
  background: var(--bg-primary);
}

/* ==================== AUTHENTICATION PAGES ==================== */
.auth-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: var(--space-xl);
  background: linear-gradient(135deg, var(--deep-sea-teal) 0%, var(--deep-sea-teal-dark) 100%);
  position: relative;
  overflow: hidden;
}

.auth-container::before {
  content: '';
  position: absolute;
  top: -50%;
  right: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, var(--soft-clay-coral-light) 0%, transparent 70%);
  opacity: 0.15;
  animation: float 20s ease-in-out infinite;
}

@keyframes float {
  0%, 100% { transform: translate(0, 0) rotate(0deg); }
  33% { transform: translate(30px, -30px) rotate(120deg); }
  66% { transform: translate(-20px, 20px) rotate(240deg); }
}

.auth-card {
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border-radius: var(--rounded-xl);
  padding: var(--space-3xl);
  width: 100%;
  max-width: 480px;
  box-shadow: var(--shadow-2xl);
  border: 1px solid var(--glass-border);
  animation: slideUp 0.6s cubic-bezier(0.16, 1, 0.3, 1);
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.auth-card h2 {
  font-size: 2rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: var(--space-sm);
  text-align: center;
}

.auth-card p {
  color: var(--text-tertiary);
  text-align: center;
  margin-bottom: var(--space-2xl);
  font-size: 0.95rem;
}

.auth-form {
  display: flex;
  flex-direction: column;
  gap: var(--space-lg);
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: var(--space-xs);
}

.form-group label {
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--text-secondary);
}

.form-group input,
.form-group select,
.form-group textarea {
  padding: var(--space-md);
  border: 2px solid var(--border-color);
  border-radius: var(--rounded);
  font-size: 1rem;
  font-family: var(--font-sans);
  background: var(--bg-tertiary);
  color: var(--text-primary);
  transition: all var(--transition-base);
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--deep-sea-teal);
  box-shadow: 0 0 0 3px rgba(0, 95, 115, 0.1);
}

.emergency-contacts-section {
  background: var(--bg-secondary);
  padding: var(--space-xl);
  border-radius: var(--rounded-lg);
  margin: var(--space-lg) 0;
  border: 1px solid var(--border-color);
}

.emergency-contacts-section h3 {
  font-size: 1.1rem;
  color: var(--text-primary);
  margin-bottom: var(--space-md);
  display: flex;
  align-items: center;
  gap: var(--space-sm);
}

.emergency-contact-item {
  background: var(--bg-tertiary);
  padding: var(--space-lg);
  border-radius: var(--rounded);
  margin-bottom: var(--space-md);
  border: 1px solid var(--border-color);
  position: relative;
}

.remove-contact-btn {
  position: absolute;
  top: var(--space-sm);
  right: var(--space-sm);
  width: 32px;
  height: 32px;
  border-radius: var(--rounded-full);
  background: var(--danger);
  color: white;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all var(--transition-fast);
}

.remove-contact-btn:hover {
  background: var(--danger-dark);
  transform: scale(1.1);
}

.add-contact-btn {
  width: 100%;
  padding: var(--space-md);
  background: var(--bg-tertiary);
  border: 2px dashed var(--border-color);
  border-radius: var(--rounded);
  color: var(--text-secondary);
  font-weight: 600;
  cursor: pointer;
  transition: all var(--transition-base);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--space-sm);
}

.add-contact-btn:hover {
  border-color: var(--deep-sea-teal);
  color: var(--deep-sea-teal);
  background: var(--bg-card);
}

.btn-primary {
  padding: var(--space-lg);
  background: linear-gradient(135deg, var(--deep-sea-teal) 0%, var(--deep-sea-teal-light) 100%);
  color: white;
  border: none;
  border-radius: var(--rounded);
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all var(--transition-base);
  box-shadow: var(--shadow-md);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.btn-primary:active {
  transform: translateY(0);
}

.btn-primary:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.auth-link {
  text-align: center;
  margin-top: var(--space-lg);
  color: var(--text-secondary);
  font-size: 0.95rem;
}

.auth-link a {
  color: var(--deep-sea-teal);
  text-decoration: none;
  font-weight: 600;
  transition: color var(--transition-fast);
}

.auth-link a:hover {
  color: var(--deep-sea-teal-light);
  text-decoration: underline;
}

.error-message {
  background: rgba(220, 38, 38, 0.1);
  border: 1px solid var(--danger);
  color: var(--danger);
  padding: var(--space-md);
  border-radius: var(--rounded);
  font-size: 0.9rem;
  text-align: center;
}

/* ==================== DASHBOARD ==================== */
.dashboard {
  min-height: 100vh;
  background: var(--bg-primary);
  padding: var(--space-2xl);
}

.dashboard-header {
  background: var(--glass-bg);
  backdrop-filter: blur(var(--glass-blur));
  border: 1px solid var(--glass-border);
  border-radius: var(--rounded-xl);
  padding: var(--space-2xl);
  margin-bottom: var(--space-2xl);
  box-shadow: var(--shadow-md);
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: var(--space-lg);
}

.header-left {
  display: flex;
  align-items: center;
  gap: var(--space-lg);
}

.user-avatar {
  width: 64px;
  height: 64px;
  border-radius: var(--rounded-full);
  background: linear-gradient(135deg, var(--deep-sea-teal), var(--soft-clay-coral));
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.75rem;
  font-weight: 700;
  box-shadow: var(--shadow-md);
}

.user-info h2 {
  font-size: 1.75rem;
  color: var(--text-primary);
  margin-bottom: var(--space-xs);
}

.user-info p {
  color: var(--text-tertiary);
  font-size: 0.95rem;
}

.header-right {
  display: flex;
  gap: var(--space-md);
}

.btn-logout {
  padding: var(--space-md) var(--space-xl);
  background: var(--bg-tertiary);
  border: 2px solid var(--border-color);
  border-radius: var(--rounded);
  color: var(--text-primary);
  font-weight: 600;
  cursor: pointer;
  transition: all var(--transition-base);
}

.btn-logout:hover {
  border-color: var(--danger);
  color: var(--danger);
  transform: translateY(-2px);
}

.dashboard-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: var(--space-xl);
  margin-bottom: var(--space-2xl);
}

.dashboard-card {
  background: var(--glass-bg);
  backdrop-filter: blur(var(--glass-blur));
  border: 1px solid var(--glass-border);
  border-radius: var(--rounded-xl);
  padding: var(--space-2xl);
  box-shadow: var(--shadow-md);
  transition: all var(--transition-base);
  position: relative;
  overflow: hidden;
}

.dashboard-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, var(--deep-sea-teal), var(--soft-clay-coral));
  transform: scaleX(0);
  transform-origin: left;
  transition: transform var(--transition-base);
}

.dashboard-card:hover::before {
  transform: scaleX(1);
}

.dashboard-card:hover {
  transform: translateY(-8px);
  box-shadow: var(--shadow-xl);
}

.card-icon {
  width: 56px;
  height: 56px;
  border-radius: var(--rounded-lg);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.75rem;
  margin-bottom: var(--space-lg);
  background: linear-gradient(135deg, var(--deep-sea-teal), var(--deep-sea-teal-light));
  color: white;
  box-shadow: var(--shadow-md);
}

.dashboard-card h3 {
  font-size: 1.5rem;
  color: var(--text-primary);
  margin-bottom: var(--space-sm);
}

.dashboard-card p {
  color: var(--text-tertiary);
  margin-bottom: var(--space-xl);
  line-height: 1.6;
}

.btn-card {
  width: 100%;
  padding: var(--space-md);
  background: linear-gradient(135deg, var(--deep-sea-teal), var(--deep-sea-teal-light));
  color: white;
  border: none;
  border-radius: var(--rounded);
  font-weight: 600;
  cursor: pointer;
  transition: all var(--transition-base);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--space-sm);
}

.btn-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.btn-card.admin {
  background: linear-gradient(135deg, var(--soft-clay-coral), var(--soft-clay-coral-light));
}

/* Emergency Contacts Display */
.emergency-contacts-display {
  background: var(--bg-secondary);
  border-radius: var(--rounded-lg);
  padding: var(--space-xl);
  border: 1px solid var(--border-color);
}

.emergency-contacts-display h3 {
  font-size: 1.25rem;
  color: var(--text-primary);
  margin-bottom: var(--space-lg);
  display: flex;
  align-items: center;
  gap: var(--space-sm);
}

.contact-card {
  background: var(--bg-card);
  backdrop-filter: blur(var(--glass-blur));
  padding: var(--space-lg);
  border-radius: var(--rounded);
  margin-bottom: var(--space-md);
  border: 1px solid var(--border-color);
  display: flex;
  align-items: center;
  gap: var(--space-md);
  transition: all var(--transition-base);
}

.contact-card:hover {
  transform: translateX(4px);
  box-shadow: var(--shadow-md);
}

.contact-icon {
  width: 44px;
  height: 44px;
  border-radius: var(--rounded-full);
  background: linear-gradient(135deg, var(--deep-sea-teal), var(--soft-clay-coral));
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.25rem;
  flex-shrink: 0;
}

.contact-info {
  flex: 1;
}

.contact-info .name {
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: var(--space-xs);
}

.contact-info .phone {
  color: var(--text-tertiary);
  font-size: 0.9rem;
  font-family: var(--font-mono);
}

.contact-info .relation {
  color: var(--text-secondary);
  font-size: 0.85rem;
  margin-top: var(--space-xs);
}

/* ==================== SOS BUTTON - LIQUID CORE ==================== */
.sos-container {
  min-height: 100vh;
  background: var(--bg-primary);
  padding: var(--space-2xl);
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.back-button {
  position: absolute;
  top: var(--space-lg);
  left: var(--space-lg);
  padding: var(--space-md) var(--space-xl);
  background: var(--glass-bg);
  backdrop-filter: blur(var(--glass-blur));
  border: 2px solid var(--glass-border);
  border-radius: var(--rounded-full);
  color: var(--text-primary);
  font-weight: 600;
  cursor: pointer;
  transition: all var(--transition-base);
}

.back-button:hover {
  transform: translateX(-4px);
  border-color: var(--deep-sea-teal);
}

.sos-header {
  text-align: center;
  margin-top: var(--space-3xl);
  margin-bottom: var(--space-2xl);
}

.sos-title {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: var(--space-sm);
}

.sos-subtitle {
  font-size: 1.1rem;
  color: var(--text-tertiary);
}

.sos-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 600px;
  gap: var(--space-2xl);
}

.sos-button-wrapper {
  width: 320px;
  height: 320px;
  position: relative;
  margin: var(--space-xl) 0;
}

.sos-button {
  width: 100%;
  height: 100%;
  border-radius: var(--rounded-full);
  background: linear-gradient(135deg, var(--deep-sea-teal), var(--deep-sea-teal-light));
  border: none;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  box-shadow: var(--shadow-2xl);
  transition: all var(--transition-slow);
  animation: breathe 4s ease-in-out infinite;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

@keyframes breathe {
  0%, 100% {
    transform: scale(1);
    box-shadow: 0 0 0 0 var(--shadow-color),
                0 0 0 10px var(--shadow-color),
                0 0 0 20px var(--shadow-color);
  }
  50% {
    transform: scale(1.05);
    box-shadow: 0 0 0 10px var(--shadow-color),
                0 0 0 20px var(--shadow-color),
                0 0 0 30px var(--shadow-color);
  }
}

.sos-button::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.3) 0%, transparent 70%);
  animation: liquidFlow 6s ease-in-out infinite;
}

@keyframes liquidFlow {
  0%, 100% { transform: translate(0, 0) rotate(0deg); }
  33% { transform: translate(20px, -20px) rotate(120deg); }
  66% { transform: translate(-15px, 15px) rotate(240deg); }
}

.sos-button:active {
  transform: scale(0.95);
}

.sos-button.active {
  background: linear-gradient(135deg, var(--electric-violet), var(--electric-violet-light)) !important;
  animation: emergencyPulse 1s ease-in-out infinite, breathe 4s ease-in-out infinite;
}

@keyframes emergencyPulse {
  0%, 100% {
    box-shadow: 0 0 0 0 var(--electric-violet-glow),
                0 0 0 20px rgba(114, 9, 183, 0.4),
                0 0 0 40px rgba(114, 9, 183, 0.2);
  }
  50% {
    box-shadow: 0 0 0 20px var(--electric-violet-glow),
                0 0 0 40px rgba(114, 9, 183, 0.3),
                0 0 0 60px rgba(114, 9, 183, 0.1);
  }
}

.sos-button-icon {
  font-size: 4rem;
  margin-bottom: var(--space-md);
  filter: drop-shadow(0 4px 8px rgba(0, 0, 0, 0.3));
  position: relative;
  z-index: 1;
}

.sos-button-text {
  font-size: 1.75rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  position: relative;
  z-index: 1;
}

.sos-controls {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: var(--space-lg);
}

.voice-toggle {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: var(--space-lg) var(--space-xl);
  background: var(--glass-bg);
  backdrop-filter: blur(var(--glass-blur));
  border: 2px solid var(--glass-border);
  border-radius: var(--rounded-lg);
  cursor: pointer;
  transition: all var(--transition-base);
}

.voice-toggle:hover {
  border-color: var(--deep-sea-teal);
  transform: translateY(-2px);
}

.voice-toggle-label {
  display: flex;
  align-items: center;
  gap: var(--space-md);
  font-weight: 600;
  color: var(--text-primary);
  font-size: 1rem;
}

.voice-toggle-switch {
  width: 56px;
  height: 32px;
  background: var(--bg-tertiary);
  border-radius: var(--rounded-full);
  position: relative;
  transition: all var(--transition-base);
  border: 2px solid var(--border-color);
}

.voice-toggle-switch::after {
  content: '';
  position: absolute;
  top: 2px;
  left: 2px;
  width: 24px;
  height: 24px;
  background: var(--text-tertiary);
  border-radius: var(--rounded-full);
  transition: all var(--transition-base);
}

.voice-toggle-switch.active {
  background: var(--deep-sea-teal);
  border-color: var(--deep-sea-teal);
}

.voice-toggle-switch.active::after {
  left: 26px;
  background: white;
}

.sos-status {
  text-align: center;
  padding: var(--space-lg);
  background: var(--glass-bg);
  backdrop-filter: blur(var(--glass-blur));
  border-radius: var(--rounded-lg);
  border: 2px solid var(--glass-border);
  font-weight: 600;
  color: var(--text-primary);
}

.sos-status.emergency {
  background: rgba(114, 9, 183, 0.2);
  border-color: var(--electric-violet);
  color: var(--electric-violet-light);
  animation: statusPulse 2s ease-in-out infinite;
}

@keyframes statusPulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.8; }
}

.glass-card {
  background: var(--glass-bg);
  backdrop-filter: blur(var(--glass-blur));
  border: 2px solid var(--glass-border);
  border-radius: var(--rounded-lg);
  padding: var(--space-lg);
  box-shadow: var(--shadow-md);
  transition: all var(--transition-base);
}

.help-link {
  display: block;
  padding: var(--space-md);
  background: var(--bg-tertiary);
  border: 2px solid var(--border-color);
  border-radius: var(--rounded);
  color: var(--text-primary);
  text-decoration: none;
  font-weight: 600;
  transition: all var(--transition-base);
  text-align: center;
}

.help-link:hover {
  border-color: var(--deep-sea-teal);
  background: var(--glass-bg);
  transform: translateY(-2px);
}

/* ==================== COMPLAINT FORM ==================== */
.complaint-container {
  min-height: 100vh;
  background: var(--bg-primary);
  padding: var(--space-2xl);
}

.complaint-wizard {
  max-width: 800px;
  margin: 0 auto;
}

.wizard-progress {
  margin-bottom: var(--space-3xl);
}

.wizard-steps {
  display: flex;
  justify-content: space-between;
  position: relative;
  margin-bottom: var(--space-2xl);
}

.wizard-steps::before {
  content: '';
  position: absolute;
  top: 24px;
  left: 10%;
  right: 10%;
  height: 2px;
  background: var(--border-color);
  z-index: 0;
}

.wizard-step {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--space-sm);
  position: relative;
  z-index: 1;
  flex: 1;
}

.step-circle {
  width: 48px;
  height: 48px;
  border-radius: var(--rounded-full);
  background: var(--bg-tertiary);
  border: 3px solid var(--border-color);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 1.1rem;
  color: var(--text-tertiary);
  transition: all var(--transition-base);
}

.wizard-step.active .step-circle {
  background: var(--deep-sea-teal);
  border-color: var(--deep-sea-teal);
  color: white;
  box-shadow: 0 0 0 4px rgba(0, 95, 115, 0.2);
}

.wizard-step.completed .step-circle {
  background: var(--success);
  border-color: var(--success);
  color: white;
}

.step-label {
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--text-tertiary);
  text-align: center;
}

.wizard-step.active .step-label {
  color: var(--deep-sea-teal);
}

.complaint-form-card {
  background: var(--glass-bg);
  backdrop-filter: blur(var(--glass-blur));
  border: 2px solid var(--glass-border);
  border-radius: var(--rounded-xl);
  padding: var(--space-3xl);
  box-shadow: var(--shadow-xl);
}

.complaint-form-title {
  font-size: 1.75rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: var(--space-sm);
}

.complaint-form-subtitle {
  color: var(--text-tertiary);
  margin-bottom: var(--space-xl);
  line-height: 1.6;
}

.form-section {
  display: flex;
  flex-direction: column;
  gap: var(--space-lg);
  margin-bottom: var(--space-xl);
}

.image-upload-area {
  border: 2px dashed var(--border-color);
  border-radius: var(--rounded-lg);
  padding: var(--space-2xl);
  text-align: center;
  background: var(--bg-secondary);
  cursor: pointer;
  transition: all var(--transition-base);
}

.image-upload-area:hover {
  border-color: var(--deep-sea-teal);
  background: var(--bg-tertiary);
}

.image-upload-area input[type="file"] {
  display: none;
}

.image-preview-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  gap: var(--space-md);
  margin-top: var(--space-lg);
}

.image-preview-item {
  position: relative;
  aspect-ratio: 1;
  border-radius: var(--rounded);
  overflow: hidden;
  border: 2px solid var(--border-color);
}

.image-preview-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.image-remove-btn {
  position: absolute;
  top: var(--space-xs);
  right: var(--space-xs);
  width: 28px;
  height: 28px;
  border-radius: var(--rounded-full);
  background: var(--danger);
  color: white;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  transition: all var(--transition-fast);
}

.image-remove-btn:hover {
  background: var(--danger-dark);
  transform: scale(1.1);
}

.form-actions {
  display: flex;
  gap: var(--space-md);
  justify-content: space-between;
  margin-top: var(--space-2xl);
}

.btn-secondary {
  padding: var(--space-md) var(--space-xl);
  background: var(--bg-tertiary);
  border: 2px solid var(--border-color);
  border-radius: var(--rounded);
  color: var(--text-primary);
  font-weight: 600;
  cursor: pointer;
  transition: all var(--transition-base);
}

.btn-secondary:hover {
  border-color: var(--deep-sea-teal);
  transform: translateY(-2px);
}

.success-message {
  text-align: center;
  padding: var(--space-3xl);
}

.success-message h2 {
  color: var(--success);
  font-size: 2rem;
  margin-bottom: var(--space-md);
}

.success-message p {
  color: var(--text-secondary);
  margin-bottom: var(--space-xl);
  line-height: 1.6;
}

.review-section {
  display: flex;
  flex-direction: column;
  gap: var(--space-lg);
}

.review-item {
  padding: var(--space-lg);
  background: var(--bg-tertiary);
  border-radius: var(--rounded);
  border: 1px solid var(--border-color);
}

.review-item h3 {
  font-size: 0.9rem;
  color: var(--text-tertiary);
  margin-bottom: var(--space-xs);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.review-item p {
  color: var(--text-primary);
  font-weight: 600;
}

/* ==================== RESPONSIVE ==================== */
@media (max-width: 768px) {
  .dashboard-grid,
  .wizard-steps {
    grid-template-columns: 1fr;
  }
  
  .sos-button-wrapper {
    width: 280px;
    height: 280px;
  }
  
  .complaint-form-card {
    padding: var(--space-xl);
  }
  
  .form-actions {
    flex-direction: column;
  }
  
  .theme-toggle {
    min-width: 140px;
    font-size: 0.85rem;
  }
}

.voice-toggle input[type="checkbox"]:checked::before {
  left: 23px;
}

.btn-back {
  padding: var(--space-md) var(--space-xl);
  background: var(--bg-tertiary);
  border: 2px solid var(--border-color);
  border-radius: var(--rounded);
  color: var(--text-primary);
  font-weight: 600;
  cursor: pointer;
  transition: all var(--transition-base);
  display: flex;
  align-items: center;
  gap: var(--space-sm);
  position: fixed;
  top: var(--space-lg);
  left: var(--space-lg);
  z-index: 100;
}

.btn-back:hover {
  border-color: var(--deep-sea-teal);
  color: var(--deep-sea-teal);
  transform: translateX(-4px);
}

/* ==================== COMPLAINT FORM - WIZARD ==================== */
.complaint-wizard {
  min-height: 100vh;
  background: var(--bg-primary);
  padding: var(--space-2xl);
  max-width: 800px;
  margin: 0 auto;
}

.wizard-progress {
  display: flex;
  justify-content: space-between;
  margin-bottom: var(--space-3xl);
  position: relative;
}

.wizard-progress::before {
  content: '';
  position: absolute;
  top: 20px;
  left: 0;
  right: 0;
  height: 2px;
  background: var(--border-color);
  z-index: 0;
}

.wizard-progress-line {
  position: absolute;
  top: 20px;
  left: 0;
  height: 2px;
  background: linear-gradient(90deg, var(--deep-sea-teal), var(--soft-clay-coral));
  transition: width var(--transition-slow);
  z-index: 1;
}

.wizard-step {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--space-sm);
  position: relative;
  z-index: 2;
}

.step-circle {
  width: 40px;
  height: 40px;
  border-radius: var(--rounded-full);
  background: var(--bg-tertiary);
  border: 2px solid var(--border-color);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  color: var(--text-tertiary);
  transition: all var(--transition-base);
}

.wizard-step.active .step-circle {
  background: linear-gradient(135deg, var(--deep-sea-teal), var(--deep-sea-teal-light));
  border-color: var(--deep-sea-teal);
  color: white;
  transform: scale(1.15);
  box-shadow: var(--shadow-md);
}

.wizard-step.completed .step-circle {
  background: var(--soft-clay-coral);
  border-color: var(--soft-clay-coral);
  color: white;
}

.step-label {
  font-size: 0.85rem;
  color: var(--text-tertiary);
  font-weight: 500;
}

.wizard-step.active .step-label {
  color: var(--text-primary);
  font-weight: 600;
}

.wizard-content {
  background: var(--glass-bg);
  backdrop-filter: blur(var(--glass-blur));
  border: 1px solid var(--glass-border);
  border-radius: var(--rounded-xl);
  padding: var(--space-3xl);
  box-shadow: var(--shadow-xl);
  animation: fadeIn var(--transition-slow);
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.wizard-content h2 {
  font-size: 1.75rem;
  color: var(--text-primary);
  margin-bottom: var(--space-sm);
}

.wizard-content p {
  color: var(--text-tertiary);
  margin-bottom: var(--space-2xl);
}

.file-upload-area {
  border: 2px dashed var(--border-color);
  border-radius: var(--rounded-lg);
  padding: var(--space-3xl);
  text-align: center;
  background: var(--bg-secondary);
  cursor: pointer;
  transition: all var(--transition-base);
  position: relative;
  overflow: hidden;
}

.file-upload-area:hover {
  border-color: var(--deep-sea-teal);
  background: var(--bg-card);
  transform: translateY(-2px);
}

.file-upload-area.drag-over {
  border-color: var(--soft-clay-coral);
  background: var(--bg-card);
  transform: scale(1.02);
}

.upload-icon {
  font-size: 3rem;
  color: var(--text-tertiary);
  margin-bottom: var(--space-md);
}

.image-preview {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  gap: var(--space-md);
  margin-top: var(--space-lg);
}

.image-preview-item {
  position: relative;
  border-radius: var(--rounded);
  overflow: hidden;
  aspect-ratio: 1;
  background: var(--bg-tertiary);
  box-shadow: var(--shadow-sm);
}

.image-preview-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.remove-image-btn {
  position: absolute;
  top: var(--space-xs);
  right: var(--space-xs);
  width: 28px;
  height: 28px;
  border-radius: var(--rounded-full);
  background: var(--danger);
  color: white;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all var(--transition-fast);
  opacity: 0;
}

.image-preview-item:hover .remove-image-btn {
  opacity: 1;
}

.wizard-actions {
  display: flex;
  gap: var(--space-md);
  margin-top: var(--space-2xl);
}

.btn-wizard {
  flex: 1;
  padding: var(--space-lg);
  border-radius: var(--rounded);
  font-weight: 600;
  cursor: pointer;
  transition: all var(--transition-base);
  border: none;
}

.btn-wizard-prev {
  background: var(--bg-tertiary);
  border: 2px solid var(--border-color);
  color: var(--text-primary);
}

.btn-wizard-prev:hover {
  border-color: var(--deep-sea-teal);
  transform: translateX(-4px);
}

.btn-wizard-next,
.btn-wizard-submit {
  background: linear-gradient(135deg, var(--deep-sea-teal), var(--deep-sea-teal-light));
  color: white;
  box-shadow: var(--shadow-md);
}

.btn-wizard-next:hover,
.btn-wizard-submit:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.btn-wizard-submit {
  background: linear-gradient(135deg, var(--soft-clay-coral), var(--soft-clay-coral-light));
}

/* ==================== ADMIN DASHBOARD ==================== */
.admin-dashboard {
  min-height: 100vh;
  background: var(--bg-primary);
  padding: var(--space-2xl);
}

.admin-header {
  background: var(--glass-bg);
  backdrop-filter: blur(var(--glass-blur));
  border: 1px solid var(--glass-border);
  border-radius: var(--rounded-xl);
  padding: var(--space-xl) var(--space-2xl);
  margin-bottom: var(--space-2xl);
  box-shadow: var(--shadow-md);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--space-md);
  position: relative;
  margin-top: var(--space-3xl);
}

.admin-header .btn-back {
  position: absolute;
  top: var(--space-lg);
  left: var(--space-lg);
}

.admin-header h1 {
  font-size: 2rem;
  color: var(--text-primary);
  margin: 0;
  display: flex;
  align-items: center;
  gap: var(--space-md);
  font-weight: 700;
}

.admin-badge {
  padding: var(--space-sm) var(--space-xl);
  background: linear-gradient(135deg, var(--accent-primary), var(--accent-secondary));
  color: white;
  border-radius: var(--rounded-full);
  font-size: 0.9rem;
  font-weight: 600;
  box-shadow: var(--shadow-md);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.admin-tabs {
  display: flex;
  gap: var(--space-md);
  margin-bottom: var(--space-2xl);
  background: var(--glass-bg);
  backdrop-filter: blur(var(--glass-blur));
  padding: var(--space-sm);
  border-radius: var(--rounded-lg);
  border: 1px solid var(--glass-border);
}

.tab-btn {
  flex: 1;
  padding: var(--space-md) var(--space-lg);
  background: transparent;
  border: none;
  border-radius: var(--rounded);
  color: var(--text-secondary);
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: all var(--transition-base);
}

.tab-btn.active {
  background: linear-gradient(135deg, var(--accent-primary), var(--accent-secondary));
  color: white;
  box-shadow: var(--shadow-md);
}

.tab-btn:not(.active):hover {
  background: var(--bg-tertiary);
  color: var(--text-primary);
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: var(--space-lg);
  margin-bottom: var(--space-2xl);
}

.stat-card {
  background: var(--glass-bg);
  backdrop-filter: blur(var(--glass-blur));
  border: 1px solid var(--glass-border);
  border-radius: var(--rounded-lg);
  padding: var(--space-xl);
  box-shadow: var(--shadow-md);
  transition: all var(--transition-base);
}

.stat-card:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-lg);
}

.stat-card h3 {
  font-size: 0.9rem;
  color: var(--text-tertiary);
  margin-bottom: var(--space-md);
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.stat-number {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0;
}

.stat-card.alert {
  border-left: 4px solid var(--error);
}

.stat-card.alert .stat-number {
  color: var(--error);
}

.stat-card.warning {
  border-left: 4px solid var(--warning);
}

.stat-card.warning .stat-number {
  color: var(--warning);
}

.stat-card.success {
  border-left: 4px solid var(--success);
}

.stat-card.success .stat-number {
  color: var(--success);
}

.admin-main {
  width: 100%;
}

.sos-alerts-section,
.complaints-section {
  width: 100%;
}

.no-data {
  text-align: center;
  padding: var(--space-3xl);
  color: var(--text-tertiary);
  font-size: 1.1rem;
  background: var(--glass-bg);
  border-radius: var(--rounded-lg);
  border: 1px dashed var(--glass-border);
}

.stat-card .label {
  font-size: 0.9rem;
  color: var(--text-tertiary);
  margin-bottom: var(--space-sm);
}

.stat-card .value {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--text-primary);
}

.alerts-grid {
  display: grid;
  gap: var(--space-lg);
}

.alert-card {
  background: var(--glass-bg);
  backdrop-filter: blur(var(--glass-blur));
  border: 1px solid var(--glass-border);
  border-radius: var(--rounded-lg);
  padding: var(--space-xl);
  box-shadow: var(--shadow-md);
  transition: all var(--transition-base);
  position: relative;
  overflow: hidden;
}

.alert-card.critical {
  border-left: 4px solid var(--error);
  animation: alertPulse 2s ease-in-out infinite;
  background: linear-gradient(135deg, var(--glass-bg), rgba(239, 68, 68, 0.05));
}

.alert-card.active {
  border-left: 4px solid var(--electric-violet);
  animation: alertPulse 2s ease-in-out infinite;
}

@keyframes alertPulse {
  0%, 100% { 
    box-shadow: var(--shadow-md); 
    border-left-color: var(--error);
  }
  50% { 
    box-shadow: var(--shadow-lg), 0 0 20px rgba(239, 68, 68, 0.3);
    border-left-color: #ff0000;
  }
}

.alert-card.resolved {
  opacity: 0.7;
  border-left: 4px solid var(--success);
}

.alert-header {
  display: flex;
  justify-content: space-between;
  align-items: start;
  margin-bottom: var(--space-lg);
  padding-bottom: var(--space-md);
  border-bottom: 1px solid var(--glass-border);
}

.alert-header h3 {
  font-size: 1.25rem;
  color: var(--text-primary);
  margin: 0;
  display: flex;
  align-items: center;
  gap: var(--space-sm);
}

.alert-time {
  font-size: 0.85rem;
  color: var(--text-tertiary);
  font-weight: 500;
}

.alert-details {
  margin-bottom: var(--space-lg);
}

.alert-details p {
  margin: var(--space-sm) 0;
  color: var(--text-secondary);
  font-size: 0.95rem;
}

.alert-details strong {
  color: var(--text-primary);
  font-weight: 600;
}

.location-details {
  margin-top: var(--space-md);
  padding: var(--space-md);
  background: var(--bg-tertiary);
  border-radius: var(--rounded);
  border: 1px solid var(--glass-border);
}

.location-details p {
  margin: var(--space-xs) 0;
  font-family: monospace;
  font-size: 0.9rem;
}

.btn-map-small {
  display: inline-block;
  margin-top: var(--space-sm);
  padding: var(--space-xs) var(--space-md);
  background: linear-gradient(135deg, var(--accent-primary), var(--accent-secondary));
  color: white;
  border-radius: var(--rounded);
  text-decoration: none;
  font-size: 0.85rem;
  font-weight: 600;
  transition: all var(--transition-base);
}

.btn-map-small:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.emergency-contacts-admin {
  margin-top: var(--space-md);
  padding: var(--space-md);
  background: var(--bg-tertiary);
  border-radius: var(--rounded);
  border: 1px solid var(--glass-border);
}

.emergency-contacts-admin strong {
  display: block;
  margin-bottom: var(--space-sm);
  color: var(--text-primary);
}

.emergency-contacts-admin p {
  margin: var(--space-xs) 0;
  padding-left: var(--space-md);
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.alert-badge {
  padding: var(--space-xs) var(--space-md);
  border-radius: var(--rounded-full);
  font-size: 0.85rem;
  font-weight: 600;
}

.alert-badge.active {
  background: var(--electric-violet-glow);
  color: var(--electric-violet);
}

.alert-badge.resolved {
  background: rgba(16, 185, 129, 0.2);
  color: var(--success);
}

.alert-actions {
  display: flex;
  gap: var(--space-sm);
  margin-top: var(--space-lg);
}

.btn-alert {
  padding: var(--space-sm) var(--space-lg);
  border-radius: var(--rounded);
  border: none;
  font-weight: 600;
  cursor: pointer;
  transition: all var(--transition-base);
}

.btn-view-location {
  background: linear-gradient(135deg, var(--deep-sea-teal), var(--deep-sea-teal-light));
  color: white;
}

.btn-resolve {
  width: 100%;
  padding: var(--space-md) var(--space-lg);
  background: linear-gradient(135deg, var(--success), #059669);
  color: white;
  border: none;
  border-radius: var(--rounded);
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: all var(--transition-base);
}

.btn-resolve:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
  background: linear-gradient(135deg, #059669, var(--success));
}

/* Complaints Section */
.filter-bar {
  display: flex;
  align-items: center;
  gap: var(--space-md);
  padding: var(--space-md);
  background: var(--glass-bg);
  border-radius: var(--rounded-lg);
  border: 1px solid var(--glass-border);
  margin-bottom: var(--space-lg);
}

.filter-bar label {
  font-weight: 600;
  color: var(--text-primary);
  font-size: 0.95rem;
}

.filter-bar select {
  flex: 1;
  max-width: 300px;
  padding: var(--space-sm) var(--space-md);
  background: var(--bg-tertiary);
  border: 1px solid var(--glass-border);
  border-radius: var(--rounded);
  color: var(--text-primary);
  font-size: 0.95rem;
  cursor: pointer;
  transition: all var(--transition-base);
}

.filter-bar select:focus {
  outline: none;
  border-color: var(--accent-primary);
  box-shadow: 0 0 0 3px rgba(var(--accent-primary-rgb), 0.1);
}

.complaints-grid {
  display: grid;
  gap: var(--space-lg);
}

.complaint-card-admin {
  background: var(--glass-bg);
  backdrop-filter: blur(var(--glass-blur));
  border: 1px solid var(--glass-border);
  border-radius: var(--rounded-lg);
  padding: var(--space-xl);
  box-shadow: var(--shadow-md);
  transition: all var(--transition-base);
}

.complaint-card-admin:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-lg);
}

.complaint-header-admin {
  display: flex;
  justify-content: space-between;
  align-items: start;
  margin-bottom: var(--space-lg);
  padding-bottom: var(--space-md);
  border-bottom: 1px solid var(--glass-border);
}

.complaint-header-admin h3 {
  font-size: 1.25rem;
  color: var(--text-primary);
  margin: 0;
  flex: 1;
}

.status-badge {
  padding: var(--space-xs) var(--space-md);
  border-radius: var(--rounded-full);
  font-size: 0.75rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.status-badge.pending {
  background: rgba(251, 191, 36, 0.2);
  color: var(--warning);
}

.status-badge.investigating {
  background: rgba(59, 130, 246, 0.2);
  color: #3b82f6;
}

.status-badge.resolved {
  background: rgba(16, 185, 129, 0.2);
  color: var(--success);
}

.status-badge.closed {
  background: rgba(107, 114, 128, 0.2);
  color: #6b7280;
}

.complaint-details {
  margin-bottom: var(--space-lg);
}

.complaint-details p {
  margin: var(--space-sm) 0;
  color: var(--text-secondary);
  font-size: 0.95rem;
  line-height: 1.6;
}

.complaint-details strong {
  color: var(--text-primary);
  font-weight: 600;
}

.images-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  gap: var(--space-sm);
  margin-top: var(--space-md);
}

.images-grid img {
  width: 100%;
  aspect-ratio: 1;
  object-fit: cover;
  border-radius: var(--rounded);
  border: 1px solid var(--glass-border);
  cursor: pointer;
  transition: transform var(--transition-fast);
}

.images-grid img:hover {
  transform: scale(1.05);
  box-shadow: var(--shadow-md);
}

.admin-actions {
  display: flex;
  gap: var(--space-md);
  margin-bottom: var(--space-md);
}

.admin-actions select {
  flex: 1;
  padding: var(--space-sm) var(--space-md);
  background: var(--bg-tertiary);
  border: 1px solid var(--glass-border);
  border-radius: var(--rounded);
  color: var(--text-primary);
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  transition: all var(--transition-base);
}

.admin-actions select:focus {
  outline: none;
  border-color: var(--accent-primary);
  box-shadow: 0 0 0 3px rgba(var(--accent-primary-rgb), 0.1);
}

.admin-notes-input {
  width: 100%;
  min-height: 80px;
  padding: var(--space-md);
  background: var(--bg-tertiary);
  border: 1px solid var(--glass-border);
  border-radius: var(--rounded);
  color: var(--text-primary);
  font-size: 0.95rem;
  font-family: inherit;
  resize: vertical;
  transition: all var(--transition-base);
}

.admin-notes-input:focus {
  outline: none;
  border-color: var(--accent-primary);
  box-shadow: 0 0 0 3px rgba(var(--accent-primary-rgb), 0.1);
}

.admin-notes-input::placeholder {
  color: var(--text-tertiary);
}

.btn-alert:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

/* ==================== RESPONSIVE DESIGN ==================== */
@media (max-width: 768px) {
  :root {
    --space-xl: 1.5rem;
    --space-2xl: 2rem;
    --space-3xl: 2.5rem;
  }
  
  .dashboard,
  .sos-container,
  .complaint-wizard,
  .admin-dashboard {
    padding: var(--space-lg);
  }
  
  .auth-card {
    padding: var(--space-xl);
  }
  
  .dashboard-grid,
  .stats-grid {
    grid-template-columns: 1fr;
  }
  
  .sos-liquid-core {
    width: 260px;
    height: 260px;
  }
  
  .liquid-core-content .icon {
    font-size: 3rem;
  }
  
  .liquid-core-content .text {
    font-size: 1.25rem;
  }
  
  .theme-toggle {
    top: var(--space-md);
    right: var(--space-md);
  }
  
  .btn-back {
    top: var(--space-md);
    left: var(--space-md);
  }
  
  .wizard-progress {
    flex-direction: column;
    align-items: stretch;
  }
  
  .wizard-progress::before {
    top: 0;
    bottom: 0;
    left: 20px;
    width: 2px;
    height: auto;
  }
  
  .wizard-step {
    flex-direction: row;
    justify-content: flex-start;
  }
}

/* ==================== ANIMATIONS & MICRO-INTERACTIONS ==================== */
@keyframes shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

.loading-shimmer {
  position: relative;
  overflow: hidden;
}

.loading-shimmer::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.2),
    transparent
  );
  animation: shimmer 2s infinite;
}

/* Accessibility */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* Focus styles for keyboard navigation */
button:focus-visible,
input:focus-visible,
select:focus-visible,
textarea:focus-visible {
  outline: 3px solid var(--deep-sea-teal);
  outline-offset: 2px;
}

/* ==================== PRINT STYLES ==================== */
@media print {
  .theme-toggle,
  .btn-back,
  .btn-logout,
  .dashboard-card,
  .wizard-actions {
    display: none !important;
  }
  
  body {
    background: white !important;
    color: black !important;
  }
}
