ral-dark: #ca6702;\n  --electric-violet: #7209B7;\n  --electric-violet-light: #9d4edd;\n  --electric-violet-glow: rgba(114, 9, 183, 0.6);\n  --ice-white: #FEFEFE;\n  --ice-white-dim: #F8F9FA;\n  \n  /* Light Theme Variables */\n  --bg-primary: #FEFEFE;\n  --bg-secondary: #F8F9FA;\n  --bg-tertiary: #ffffff;\n  --bg-card: rgba(255, 255, 255, 0.85);\n  --text-primary: #0d1117;\n  --text-secondary: #374151;\n  --text-tertiary: #6b7280;\n  --border-color: rgba(0, 95, 115, 0.12);\n  --shadow-color: rgba(0, 0, 0, 0.08);\n  --glass-bg: rgba(255, 255, 255, 0.75);\n  --glass-border: rgba(255, 255, 255, 0.35);\n  --overlay-bg: rgba(0, 0, 0, 0.5);\n  \n  /* Legacy support */\n  --primary: #005F73;\n  --primary-dark: #003d47;\n  --primary-light: #0a9396;\n  --secondary: #EE9B00;\n  --accent: #7209B7;\n  \n  /* Accent colors for UI elements */\n  --accent-primary: #005F73;\n  --accent-secondary: #0a9396;\n  --accent-primary-rgb: 0, 95, 115;\n  \n  /* Status Colors */\n  --error: #dc2626;\n  --danger-dark: #b91c1c;\n  --success: #10b981;\n  --warning: #f59e0b;\n  --info: #3b82f6;\n  \n  /* Neutral Palette */\n  --white: #ffffff;\n  --gray-50: #f9fafb;\n  --gray-100: #f3f4f6;\n  --gray-200: #e5e7eb;\n  --gray-300: #d1d5db;\n  --gray-400: #9ca3af;\n  --gray-500: #6b7280;\n  --gray-600: #4b5563;\n  --gray-700: #374151;\n  --gray-800: #1f2937;\n  --gray-900: #111827;\n  \n  /* Glassmorphism */\n  --glass-blur: 12px;\n  --glass-opacity: 0.75;\n  \n  /* Shadows - Ethereal */\n  --shadow-sm: 0 2px 4px var(--shadow-color);\n  --shadow: 0 4px 12px var(--shadow-color);\n  --shadow-md: 0 6px 20px var(--shadow-color);\n  --shadow-lg: 0 10px 30px var(--shadow-color);\n  --shadow-xl: 0 15px 45px var(--shadow-color);\n  --shadow-2xl: 0 25px 60px rgba(0, 95, 115, 0.15);\n  \n  /* Border Radius - Fluid Geometry */\n  --rounded-sm: 0.5rem;\n  --rounded: 0.75rem;\n  --rounded-md: 1rem;\n  --rounded-lg: 1.5rem;\n  --rounded-xl: 2rem;\n  --rounded-full: 9999px;\n  \n  /* Spacing Scale - Improved */\n  --space-xs: 0.5rem;\n  --space-sm: 0.75rem;\n  --space-md: 1rem;\n  --space-lg: 1.5rem;\n  --space-xl: 2rem;\n  --space-2xl: 3rem;\n  --space-3xl: 4rem;\n  \n  /* Typography */\n  --font-sans: -apple-system, BlinkMacSystemFont, 'Inter', 'Segoe UI', 'Helvetica Neue', Arial, sans-serif;\n  --font-mono: 'SF Mono', Monaco, 'Cascadia Code', 'Roboto Mono', monospace;\n  \n  /* Transitions */\n  --transition-fast: 150ms ease;\n  --transition-base: 250ms cubic-bezier(0.4, 0, 0.2, 1);\n  --transition-slow: 400ms cubic-bezier(0.4, 0, 0.2, 1);\n}\n\n/* Light Theme Variants */\n[data-theme=\"light\"] {\n  --deep-sea-teal: #005F73;\n  --deep-sea-teal-light: #0a9396;\n  --deep-sea-teal-dark: #003d47;\n  --soft-clay-coral: #EE9B00;\n  --soft-clay-coral-light: #f4a42c;\n  --soft-clay-coral-dark: #ca6702;\n  \n  --accent-primary: #005F73;\n  --accent-secondary: #0a9396;\n  --accent-primary-rgb: 0, 95, 115;\n  --error: #dc2626;\n  \n  --bg-primary: #FEFEFE;\n  --bg-secondary: #F8F9FA;\n  --bg-tertiary: #ffffff;\n  --bg-card: rgba(255, 255, 255, 0.85);\n  --text-primary: #0d1117;\n  --text-secondary: #374151;\n  --text-tertiary: #6b7280;\n  --border-color: rgba(0, 95, 115, 0.12);\n  --shadow-color: rgba(0, 0, 0, 0.08);\n  --glass-bg: rgba(255, 255, 255, 0.75);\n  --glass-border: rgba(255, 255, 255, 0.35);\n  --overlay-bg: rgba(0, 0, 0, 0.5);\n}\n\n[data-theme=\"light-mint\"] {\n  --deep-sea-teal: #06d6a0;\n  --deep-sea-teal-light: #38e4ae;\n  --deep-sea-teal-dark: #05a077;\n  --soft-clay-coral: #ff6b6b;\n  --soft-clay-coral-light: #ff8787;\n  --soft-clay-coral-dark: #ee5a52;\n  \n  --accent-primary: #06d6a0;\n  --accent-secondary: #38e4ae;\n  --accent-primary-rgb: 6, 214, 160;\n  --error: #dc2626;\n  \n  --bg-primary: #f0fff4;\n  --bg-secondary: #e6fffa;\n  --bg-tertiary: #ffffff;\n  --bg-card: rgba(255, 255, 255, 0.9);\n  --text-primary: #0a3d2c;\n  --text-secondary: #2d6a4f;\n  --text-tertiary: #52796f;\n  --border-color: rgba(6, 214, 160, 0.2);\n  --shadow-color: rgba(6, 214, 160, 0.15);\n  --glass-bg: rgba(255, 255, 255, 0.8);\n  --glass-border: rgba(6, 214, 160, 0.2);\n  --overlay-bg: rgba(0, 0, 0, 0.5);\n}\n\n[data-theme=\"light-lavender\"] {\n  --deep-sea-teal: #7209b7;\n  --deep-sea-teal-light: #9d4edd;\n  --deep-sea-teal-dark: #560bad;\n  --soft-clay-coral: #ff006e;\n  --soft-clay-coral-light: #ff4d8f;\n  --soft-clay-coral-dark: #d90368;\n  \n  --accent-primary: #7209b7;\n  --accent-secondary: #9d4edd;\n  --accent-primary-rgb: 114, 9, 183;\n  --error: #dc2626;\n  \n  --bg-primary: #f8f0ff;\n  --bg-secondary: #f3e5ff;\n  --bg-tertiary: #ffffff;\n  --bg-card: rgba(255, 255, 255, 0.9);\n  --text-primary: #2d0a4e;\n  --text-secondary: #4a1772;\n  --text-tertiary: #6b2d91;\n  --border-color: rgba(114, 9, 183, 0.2);\n  --shadow-color: rgba(114, 9, 183, 0.12);\n  --glass-bg: rgba(255, 255, 255, 0.8);\n  --glass-border: rgba(157, 78, 221, 0.2);\n  --overlay-bg: rgba(0, 0, 0, 0.5);\n}\n\n[data-theme=\"light-coral\"] {\n  --deep-sea-teal: #ff6b6b;\n  --deep-sea-teal-light: #ff8787;\n  --deep-sea-teal-dark: #ee5a52;\n  --soft-clay-coral: #ffa500;\n  --soft-clay-coral-light: #ffb733;\n  --soft-clay-coral-dark: #ff8c00;\n  \n  --accent-primary: #ff6b6b;\n  --accent-secondary: #ffa500;\n  --accent-primary-rgb: 255, 107, 107;\n  --error: #dc2626;\n  \n  --bg-primary: #fff5f5;\n  --bg-secondary: #ffe5e5;\n  --bg-tertiary: #ffffff;\n  --bg-card: rgba(255, 255, 255, 0.9);\n  --text-primary: #4a0e0e;\n  --text-secondary: #7c1d1d;\n  --text-tertiary: #a83737;\n  --border-color: rgba(255, 107, 107, 0.2);\n  --shadow-color: rgba(255, 107, 107, 0.12);\n  --glass-bg: rgba(255, 255, 255, 0.8);\n  --glass-border: rgba(255, 135, 135, 0.2);\n  --overlay-bg: rgba(0, 0, 0, 0.5);\n}\n\n/* Dark Theme Variant: Sunset Blaze  */\n[data-theme=\"dark-sunset\"] {\n  --deep-sea-teal: #ff006e;\n  --deep-sea-teal-light: #ff4d8f;\n  --deep-sea-teal-dark: #d90368;\n  --soft-clay-coral: #fb5607;\n  --soft-clay-coral-light: #ff8500;\n  --soft-clay-coral-dark: #e63946;\n  \n  --accent-primary: #ff006e;\n  --accent-secondary: #fb5607;\n  --accent-primary-rgb: 255, 0, 110;\n  --error: #ff4d8f;\n  \n  --bg-primary: #1a0a0f;\n  --bg-secondary: #2d1b27;\n  --bg-tertiary: #4a2b3e;\n  --bg-card: rgba(74, 43, 62, 0.9);\n  --text-primary: #ffe5ec;\n  --text-secondary: #ffccd5;\n  --text-tertiary: #ffb3c1;\n  --border-color: rgba(255, 0, 110, 0.4);\n  --shadow-color: rgba(26, 10, 15, 0.8);\n  --glass-bg: rgba(45, 27, 39, 0.8);\n  --glass-border: rgba(255, 77, 143, 0.3);\n  --overlay-bg: rgba(26, 10, 15, 0.9);\n  \n  --shadow-sm: 0 2px 8px rgba(255, 0, 110, 0.3);\n  --shadow: 0 4px 16px rgba(255, 0, 110, 0.4);\n  --shadow-md: 0 8px 24px rgba(255, 0, 110, 0.5);\n  --shadow-lg: 0 12px 40px rgba(255, 0, 110, 0.6);\n  --shadow-xl: 0 20px 60px rgba(255, 0, 110, 0.7);\n  --shadow-2xl: 0 30px 80px rgba(255, 0, 110, 0.8);\n}\n\n/* Dark Theme Variant: Electric Dreams  */\n[data-theme=\"dark-electric\"] {\n  --deep-sea-teal: #00ff41;\n  --deep-sea-teal-light: #39ff14;\n  --deep-sea-teal-dark: #00cc33;\n  --soft-clay-coral: #00d9ff;\n  --soft-clay-coral-light: #00f5ff;\n  --soft-clay-coral-dark: #00a8cc;\n  \n  --accent-primary: #00ff41;\n  --accent-secondary: #00d9ff;\n  --accent-primary-rgb: 0, 255, 65;\n  --error: #ff006e;\n  \n  --bg-primary: #000814;\n  --bg-secondary: #001d3d;\n  --bg-tertiary: #003566;\n  --bg-card: rgba(0, 53, 102, 0.9);\n  --text-primary: #e0fbfc;\n  --text-secondary: #c7f9ff;\n  --text-tertiary: #98f5e1;\n  --border-color: rgba(0, 255, 65, 0.5);\n  --shadow-color: rgba(0, 8, 20, 0.9);\n  --glass-bg: rgba(0, 29, 61, 0.8);\n  --glass-border: rgba(0, 255, 65, 0.3);\n  --overlay-bg: rgba(0, 8, 20, 0.95);\n  \n  --shadow-sm: 0 2px 12px rgba(0, 255, 65, 0.4);\n  --shadow: 0 4px 20px rgba(0, 255, 65, 0.5);\n  --shadow-md: 0 8px 32px rgba(0, 255, 65, 0.6);\n  --shadow-lg: 0 12px 48px rgba(0, 255, 65, 0.7);\n  --shadow-xl: 0 20px 72px rgba(0, 255, 65, 0.8);\n  --shadow-2xl: 0 30px 96px rgba(0, 255, 65, 0.9);\n}\n\n/* Dark Theme Variant: Aurora Borealis  */\n[data-theme=\"dark-aurora\"] {\n  --deep-sea-teal: #4cc9f0;\n  --deep-sea-teal-light: #7209b7;\n  --deep-sea-teal-dark: #3a0ca3;\n  --soft-clay-coral: #f72585;\n  --soft-clay-coral-light: #b5179e;\n  --soft-clay-coral-dark: #560bad;\n  \n  --accent-primary: #7209b7;\n  --accent-secondary: #f72585;\n  --accent-primary-rgb: 114, 9, 183;\n  --error: #ff006e;\n  \n  --bg-primary: #0f0326;\n  --bg-secondary: #1a0b3e;\n  --bg-tertiary: #2d1b69;\n  --bg-card: rgba(45, 27, 105, 0.9);\n  --text-primary: #e0c3fc;\n  --text-secondary: #d8bbff;\n  --text-tertiary: #c8b6ff;\n  --border-color: rgba(76, 201, 240, 0.5);\n  --shadow-color: rgba(15, 3, 38, 0.9);\n  --glass-bg: rgba(26, 11, 62, 0.8);\n  --glass-border: rgba(76, 201, 240, 0.3);\n  --overlay-bg: rgba(15, 3, 38, 0.95);\n  \n  --shadow-sm: 0 2px 12px rgba(114, 9, 183, 0.4);\n  --shadow: 0 4px 20px rgba(114, 9, 183, 0.5);\n  --shadow-md: 0 8px 32px rgba(114, 9, 183, 0.6);\n  --shadow-lg: 0 12px 48px rgba(114, 9, 183, 0.7);\n  --shadow-xl: 0 20px 72px rgba(114, 9, 183, 0.8);\n  --shadow-2xl: 0 30px 96px rgba(114, 9, 183, 0.9);\n}\n\n/* Dark Theme Variant: Toxic Glow  */\n[data-theme=\"dark-toxic\"] {\n  --deep-sea-teal: #ccff00;\n  --deep-sea-teal-light: #d4ff1a;\n  --deep-sea-teal-dark: #aacc00;\n  --soft-clay-coral: #ff006e;\n  --soft-clay-coral-light: #ff4081;\n  --soft-clay-coral-dark: #c51162;\n  \n  --accent-primary: #ccff00;\n  --accent-secondary: #ff006e;\n  --accent-primary-rgb: 204, 255, 0;\n  --error: #ff4081;\n  \n  --bg-primary: #0d1b0d;\n  --bg-secondary: #1a331a;\n  --bg-tertiary: #2d4d2d;\n  --bg-card: rgba(45, 77, 45, 0.9);\n  --text-primary: #e6ffe6;\n  --text-secondary: #ccffcc;\n  --text-tertiary: #b3ffb3;\n  --border-color: rgba(204, 255, 0, 0.5);\n  --shadow-color: rgba(13, 27, 13, 0.9);\n  --glass-bg: rgba(26, 51, 26, 0.8);\n  --glass-border: rgba(204, 255, 0, 0.3);\n  --overlay-bg: rgba(13, 27, 13, 0.95);\n  \n  --shadow-sm: 0 2px 12px rgba(204, 255, 0, 0.4);\n  --shadow: 0 4px 20px rgba(204, 255, 0, 0.5);\n  --shadow-md: 0 8px 32px rgba(204, 255, 0, 0.6);\n  --shadow-lg: 0 12px 48px rgba(204, 255, 0, 0.7);\n  --shadow-xl: 0 20px 72px rgba(204, 255, 0, 0.8);\n  --shadow-2xl: 0 30px 96px rgba(204, 255, 0, 0.9);\n}\n\n/* Dark Theme Variant: Lava Flow  */\n[data-theme=\"dark-lava\"] {\n  --deep-sea-teal: #ff4500;\n  --deep-sea-teal-light: #ff6b35;\n  --deep-sea-teal-dark: #cc3700;\n  --soft-clay-coral: #ffdd00;\n  --soft-clay-coral-light: #ffe654;\n  --soft-clay-coral-dark: #ccb100;\n  \n  --accent-primary: #ff4500;\n  --accent-secondary: #ffdd00;\n  --accent-primary-rgb: 255, 69, 0;\n  --error: #ff006e;\n  \n  --bg-primary: #1a0500;\n  --bg-secondary: #330d00;\n  --bg-tertiary: #4d1a00;\n  --bg-card: rgba(77, 26, 0, 0.9);\n  --text-primary: #fff5e6;\n  --text-secondary: #ffe6cc;\n  --text-tertiary: #ffd4a3;\n  --border-color: rgba(255, 69, 0, 0.5);\n  --shadow-color: rgba(26, 5, 0, 0.9);\n  --glass-bg: rgba(51, 13, 0, 0.8);\n  --glass-border: rgba(255, 107, 53, 0.3);\n  --overlay-bg: rgba(26, 5, 0, 0.95);\n  \n  --shadow-sm: 0 2px 12px rgba(255, 69, 0, 0.4);\n  --shadow: 0 4px 20px rgba(255, 69, 0, 0.5);\n  --shadow-md: 0 8px 32px rgba(255, 69, 0, 0.6);\n  --shadow-lg: 0 12px 48px rgba(255, 69, 0, 0.7);\n  --shadow-xl: 0 20px 72px rgba(255, 69, 0, 0.8);\n  --shadow-2xl: 0 30px 96px rgba(255, 69, 0, 0.9);\n}\n\n/* Dark Theme Variant: Deep Sea  */\n[data-theme=\"dark-deepsea\"] {\n  --deep-sea-teal: #00fff5;\n  --deep-sea-teal-light: #00e5d4;\n  --deep-sea-teal-dark: #00ccbc;\n  --soft-clay-coral: #4361ee;\n  --soft-clay-coral-light: #7209b7;\n  --soft-clay-coral-dark: #3f37c9;\n  \n  --accent-primary: #00fff5;\n  --accent-secondary: #4361ee;\n  --accent-primary-rgb: 0, 255, 245;\n  --error: #ff006e;\n  \n  --bg-primary: #000814;\n  --bg-secondary: #001233;\n  --bg-tertiary: #002855;\n  --bg-card: rgba(0, 40, 85, 0.9);\n  --text-primary: #caf0f8;\n  --text-secondary: #90e0ef;\n  --text-tertiary: #48cae4;\n  --border-color: rgba(0, 255, 245, 0.5);\n  --shadow-color: rgba(0, 8, 20, 0.9);\n  --glass-bg: rgba(0, 18, 51, 0.8);\n  --glass-border: rgba(0, 255, 245, 0.3);\n  --overlay-bg: rgba(0, 8, 20, 0.95);\n  \n  --shadow-sm: 0 2px 12px rgba(0, 255, 245, 0.4);\n  --shadow: 0 4px 20px rgba(0, 255, 245, 0.5);\n  --shadow-md: 0 8px 32px rgba(0, 255, 245, 0.6);\n  --shadow-lg: 0 12px 48px rgba(0, 255, 245, 0.7);\n  --shadow-xl: 0 20px 72px rgba(0, 255, 245, 0.8);\n  --shadow-2xl: 0 30px 96px rgba(0, 255, 245, 0.9);\n}\n\n/* Auto Dark Mode based on system preference - Aurora Theme */\n@media (prefers-color-scheme: dark) {\n  :root:not([data-theme=\"light\"]) {\n    --deep-sea-teal: #4cc9f0;\n    --deep-sea-teal-light: #7209b7;\n    --deep-sea-teal-dark: #3a0ca3;\n    --soft-clay-coral: #f72585;\n    --soft-clay-coral-light: #b5179e;\n    --soft-clay-coral-dark: #560bad;\n    \n    --bg-primary: #0f0326;\n    --bg-secondary: #1a0b3e;\n    --bg-tertiary: #2d1b69;\n    --bg-card: rgba(45, 27, 105, 0.9);\n    --text-primary: #e0c3fc;\n    --text-secondary: #d8bbff;\n    --text-tertiary: #c8b6ff;\n    --border-color: rgba(76, 201, 240, 0.5);\n    --shadow-color: rgba(15, 3, 38, 0.9);\n    --glass-bg: rgba(26, 11, 62, 0.8);\n    --glass-border: rgba(76, 201, 240, 0.3);\n    --overlay-bg: rgba(15, 3, 38, 0.95);\n    \n    --shadow-sm: 0 2px 12px rgba(114, 9, 183, 0.4);\n    --shadow: 0 4px 20px rgba(114, 9, 183, 0.5);\n    --shadow-md: 0 8px 32px rgba(114, 9, 183, 0.6);\n    --shadow-lg: 0 12px 48px rgba(114, 9, 183, 0.7);\n    --shadow-xl: 0 20px 72px rgba(114, 9, 183, 0.8);\n    --shadow-2xl: 0 30px 96px rgba(114, 9, 183, 0.9);\n  }\n}\n\n/* Global Reset */\n*, *::before, *::after {\n  box-sizing: border-box;\n  margin: 0;\n  padding: 0;\n}\n\nhtml {\n  font-size: 16px;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  scroll-behavior: smooth;\n}\n\nbody {\n  font-family: var(--font-sans);\n  background: var(--bg-primary);\n  color: var(--text-primary);\n  line-height: 1.6;\n  min-height: 100vh;\n  transition: background-color var(--transition-base), color var(--transition-base);\n}\n\n/* Theme Selector */\n.theme-selector-container {\n  position: fixed;\n  top: var(--space-lg);\n  right: var(--space-lg);\n  z-index: 1000;\n}\n\n.theme-toggle {\n  min-width: 160px;\n  height: 50px;\n  padding: 0 var(--space-lg);\n  border-radius: var(--rounded-full);\n  background: var(--glass-bg);\n  backdrop-filter: blur(var(--glass-blur));\n  border: 2px solid var(--glass-border);\n  box-shadow: var(--shadow-md);\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  transition: all var(--transition-base);\n  color: var(--text-primary);\n  font-weight: 600;\n  font-size: 0.95rem;\n}\n\n.theme-toggle:hover {\n  transform: translateY(-2px);\n  box-shadow: var(--shadow-lg);\n  border-color: var(--deep-sea-teal);\n}\n\n.theme-menu {\n  position: absolute;\n  top: 60px;\n  right: 0;\n  min-width: 280px;\n  background: var(--glass-bg);\n  backdrop-filter: blur(20px);\n  border: 2px solid var(--glass-border);\n  border-radius: var(--rounded-lg);\n  box-shadow: var(--shadow-xl);\n  padding: var(--space-sm);\n  animation: slideDown 0.3s cubic-bezier(0.16, 1, 0.3, 1);\n}\n\n@keyframes slideDown {\n  from {\n    opacity: 0;\n    transform: translateY(-10px);\n  }\n  to {\n    opacity: 1;\n    transform: translateY(0);\n  }\n}\n\n.theme-option {\n  width: 100%;\n  padding: var(--space-md);\n  background: transparent;\n  border: 2px solid transparent;\n  border-radius: var(--rounded);\n  cursor: pointer;\n  transition: all var(--transition-fast);\n  text-align: left;\n  margin-bottom: var(--space-xs);\n  display: flex;\n  flex-direction: column;\n  gap: var(--space-xs);\n}\n\n.theme-option:hover {\n  background: var(--bg-tertiary);\n  border-color: var(--deep-sea-teal);\n  transform: translateX(4px);\n}\n\n.theme-option.active {\n  background: var(--bg-tertiary);\n  border-color: var(--deep-sea-teal);\n}\n\n.theme-option-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n\n.theme-option-label {\n  font-weight: 600;\n  color: var(--text-primary);\n  font-size: 0.95rem;\n}\n\n.theme-checkmark {\n  color: var(--deep-sea-teal);\n  font-weight: 700;\n  font-size: 1.1rem;\n}\n\n.theme-option-description {\n  font-size: 0.85rem;\n  color: var(--text-tertiary);\n  line-height: 1.4;\n}\n\n/* Loading State */\n.loading {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  min-height: 100vh;\n  font-size: 1.5rem;\n  color: var(--text-secondary);\n  background: var(--bg-primary);\n}\n\n/* ==================== AUTHENTICATION PAGES ==================== */\n.auth-container {\n  min-height: 100vh;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  padding: var(--space-xl);\n  background: linear-gradient(135deg, var(--deep-sea-teal) 0%, var(--deep-sea-teal-dark) 100%);\n  position: relative;\n  overflow: hidden;\n}\n\n.auth-container::before {\n  content: '';\n  position: absolute;\n  top: -50%;\n  right: -50%;\n  width: 200%;\n  height: 200%;\n  background: radial-gradient(circle, var(--soft-clay-coral-light) 0%, transparent 70%);\n  opacity: 0.15;\n  animation: float 20s ease-in-out infinite;\n}\n\n@keyframes float {\n  0%, 100% { transform: translate(0, 0) rotate(0deg); }\n  33% { transform: translate(30px, -30px) rotate(120deg); }\n  66% { transform: translate(-20px, 20px) rotate(240deg); }\n}\n\n.auth-card {\n  background: var(--glass-bg);\n  backdrop-filter: blur(20px);\n  border-radius: var(--rounded-xl);\n  padding: var(--space-3xl);\n  width: 100%;\n  max-width: 480px;\n  box-shadow: var(--shadow-2xl);\n  border: 1px solid var(--glass-border);\n  animation: slideUp 0.6s cubic-bezier(0.16, 1, 0.3, 1);\n}\n\n@keyframes slideUp {\n  from {\n    opacity: 0;\n    transform: translateY(30px);\n  }\n  to {\n    opacity: 1;\n    transform: translateY(0);\n  }\n}\n\n.auth-card h2 {\n  font-size: 2rem;\n  font-weight: 700;\n  color: var(--text