play: flex;\n  align-items: center;\n  gap: var(--space-sm);\n}\n\n.contact-card {\n  background: var(--bg-card);\n  backdrop-filter: blur(var(--glass-blur));\n  padding: var(--space-lg);\n  border-radius: var(--rounded);\n  margin-bottom: var(--space-md);\n  border: 1px solid var(--border-color);\n  display: flex;\n  align-items: center;\n  gap: var(--space-md);\n  transition: all var(--transition-base);\n}\n\n.contact-card:hover {\n  transform: translateX(4px);\n  box-shadow: var(--shadow-md);\n}\n\n.contact-icon {\n  width: 44px;\n  height: 44px;\n  border-radius: var(--rounded-full);\n  background: linear-gradient(135deg, var(--deep-sea-teal), var(--soft-clay-coral));\n  color: white;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  font-size: 1.25rem;\n  flex-shrink: 0;\n}\n\n.contact-info {\n  flex: 1;\n}\n\n.contact-info .name {\n  font-weight: 600;\n  color: var(--text-primary);\n  margin-bottom: var(--space-xs);\n}\n\n.contact-info .phone {\n  color: var(--text-tertiary);\n  font-size: 0.9rem;\n  font-family: var(--font-mono);\n}\n\n.contact-info .relation {\n  color: var(--text-secondary);\n  font-size: 0.85rem;\n  margin-top: var(--space-xs);\n}\n\n/* ==================== SOS BUTTON - LIQUID CORE ==================== */\n.sos-container {\n  min-height: 100vh;\n  background: var(--bg-primary);\n  padding: var(--space-2xl);\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  position: relative;\n}\n\n.back-button {\n  position: absolute;\n  top: var(--space-lg);\n  left: var(--space-lg);\n  padding: var(--space-md) var(--space-xl);\n  background: var(--glass-bg);\n  backdrop-filter: blur(var(--glass-blur));\n  border: 2px solid var(--glass-border);\n  border-radius: var(--rounded-full);\n  color: var(--text-primary);\n  font-weight: 600;\n  cursor: pointer;\n  transition: all var(--transition-base);\n}\n\n.back-button:hover {\n  transform: translateX(-4px);\n  border-color: var(--deep-sea-teal);\n}\n\n.sos-header {\n  text-align: center;\n  margin-top: var(--space-3xl);\n  margin-bottom: var(--space-2xl);\n}\n\n.sos-title {\n  font-size: 2.5rem;\n  font-weight: 700;\n  color: var(--text-primary);\n  margin-bottom: var(--space-sm);\n}\n\n.sos-subtitle {\n  font-size: 1.1rem;\n  color: var(--text-tertiary);\n}\n\n.sos-main {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  width: 100%;\n  max-width: 600px;\n  gap: var(--space-2xl);\n}\n\n.sos-button-wrapper {\n  width: 320px;\n  height: 320px;\n  position: relative;\n  margin: var(--space-xl) 0;\n}\n\n.sos-button {\n  width: 100%;\n  height: 100%;\n  border-radius: var(--rounded-full);\n  background: linear-gradient(135deg, var(--deep-sea-teal), var(--deep-sea-teal-light));\n  border: none;\n  cursor: pointer;\n  position: relative;\n  overflow: hidden;\n  box-shadow: var(--shadow-2xl);\n  transition: all var(--transition-slow);\n  animation: breathe 4s ease-in-out infinite;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  color: white;\n}\n\n@keyframes breathe {\n  0%, 100% {\n    transform: scale(1);\n    box-shadow: 0 0 0 0 var(--shadow-color),\n                0 0 0 10px var(--shadow-color),\n                0 0 0 20px var(--shadow-color);\n  }\n  50% {\n    transform: scale(1.05);\n    box-shadow: 0 0 0 10px var(--shadow-color),\n                0 0 0 20px var(--shadow-color),\n                0 0 0 30px var(--shadow-color);\n  }\n}\n\n.sos-button::before {\n  content: '';\n  position: absolute;\n  top: -50%;\n  left: -50%;\n  width: 200%;\n  height: 200%;\n  background: radial-gradient(circle, rgba(255, 255, 255, 0.3) 0%, transparent 70%);\n  animation: liquidFlow 6s ease-in-out infinite;\n}\n\n@keyframes liquidFlow {\n  0%, 100% { transform: translate(0, 0) rotate(0deg); }\n  33% { transform: translate(20px, -20px) rotate(120deg); }\n  66% { transform: translate(-15px, 15px) rotate(240deg); }\n}\n\n.sos-button:active {\n  transform: scale(0.95);\n